}


.component-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 0px;
}

.component-table th,
.component-table td {
  border-bottom: 1px solid #eee;
  padding: 8px 2px;
  text-align: left;
  vertical-align: middle;
}

.component-table th {
  font-weight: bold;
  font-size: 0.9em;
  color: #555;
}

.component-table-cell {
  font-size: 0.9em;
  color: #666;
}

.component-table-image-cell {
  display: flex;
  align-items: center;
}

.component-color-bar {
  display: inline-block;
  width: 5px;
