.slider-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

/* Progress checkpoints */
.progress-checkpoint {
  transition: all 0.2s ease;
}

.progress-checkpoint:hover {
  /* width: 12px !important; */
  /* height: 12px !important; */
  background-color: rgba(255, 176, 4, 1) !important;
  box-shadow: 0 0 8px rgba(255, 176, 4, 0.6);
}


.progress-slider {
  flex: 1.0; /* Takes 90% of the available space */
  height: 6px;
  margin-right: 10px;
  -webkit-appearance: none;
  appearance: none;
  background: #ddd;
  border-radius: 3px;
  outline: none;
}

.progress-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #4a80f5;
  cursor: pointer;
  position: relative;
  z-index: 10;
}

.progress-slider::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #4a80f5;
  cursor: pointer;
  border: none;
  position: relative;
  z-index: 10;
}

.slider-value {
  text-align: right;
  font-weight: 500;
  min-width: 40px;
  font-size: 14px;
}

.fullscreen-slider {
  position: absolute !important;
  bottom: 20px;
  left: 20px;
  right: 20px;
  width: auto !important;
  max-width: none !important;
  padding: 6px 16px !important;
  z-index: 9999;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  border: none;
  display: flex !important;
  flex-direction: row !important;
  align-items: center !important;
  gap: 15px;
  transition: background 0.3s ease;
}

.fullscreen-slider:hover {
  background: rgba(0, 0, 0, 0.6);
}

.fullscreen-slider:active,
.fullscreen-slider:has(.progress-slider:active) {
  background: rgba(0, 0, 0, 0.6);
}



.fullscreen-slider .card-title {
  display: none;
}

.fullscreen-slider .slider-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}
.fullscreen-slider .progress-slider {
  flex: 1;
  width: 100%;
  height: 6px;
  margin: 0;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

.fullscreen-slider .progress-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  background: rgba(255, 215, 0, 0.6);
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease;
}

.fullscreen-slider .progress-slider::-webkit-slider-thumb:hover {
  transform: scale(1.2);
  background: var(--highlight-primary);
}

.fullscreen-slider .progress-slider::-webkit-slider-thumb:active {
  background: var(--highlight-primary);
}

.fullscreen-slider .progress-slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  background: rgba(255, 215, 0, 0.6);
  border-radius: 50%;
  cursor: pointer;
  border: none;
  transition: all 0.2s ease;
}

.fullscreen-slider .progress-slider::-moz-range-thumb:hover {
  transform: scale(1.2);
  background: var(--highlight-primary);
}

.fullscreen-slider .progress-slider::-moz-range-thumb:active {
  background: var(--highlight-primary);
}

.toggle-fullscreen-btn {
  box-shadow: none;
  padding: 5.88px 5.88px 0px 5.88px;
  font-size: 20px;
}

.toggle-fullscreen-btn:hover {
  background-color: var(--hover-bg);
  cursor: pointer;
}

.fullscreen-slider .toggle-fullscreen-btn {
  margin-left: 10px;
}

.fullscreen-slider .slider-value {
  color: white;
  font-size: 14px;
  font-weight: 500;
  min-width: 40px;
  text-align: right;
  margin: 0;
}

/* Playback controls */
.playback-controls {
  display: flex;
  gap: 2px;
  margin-right: 0px;
  align-items: center;
}

/* Orientation toggle button - hidden by default, shown on mobile */
.orientation-toggle-button {
  display: none;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0px;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.7);
  font-size: var(--font-size-large);
  transition: all 0.2s ease;
  min-width: 0px;
}

.orientation-toggle-button:hover {
  color: rgba(255, 255, 255, 1);
  transform: scale(1.1);
}

.orientation-toggle-button:active {
  transform: scale(0.95);
}

.playback-button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.7);
  font-size: var(--font-size-xlarge);
  transition: all 0.2s ease;
  /* line-height: 20px; */
  min-width: 0px;
}

.playback-button:hover {
  color: rgba(255, 255, 255, 1);
  transform: scale(1.1);
}

.playback-button:active {
  transform: scale(0.95);
}

.playback-button.speed-button {
  font-size: var(--font-size-large);
  /* min-width: 36px; */
  font-family: 'Lato', 'Roboto', 'Arial', sans-serif;
}

.fullscreen-slider .playback-button {
  color: rgba(255, 255, 255, 0.8);
}

.fullscreen-slider .playback-button:hover {
  color: rgba(255, 255, 255, 1);
}

.fullscreen-slider .playback-button.speed-button {
  font-size: var(--font-size-large);
}

/* Vertical slider on right side */
.vertical-slider {
  position: absolute !important;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 50px !important;
  height: 90% !important;
  max-height: calc(100% - 40px) !important;
  padding: 0px 0px !important;
  z-index: 9999;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 10px;
  border: none;
  display: flex !important;
  flex-direction: column !important;
  justify-content: center !important;
  align-items: center !important;
}

.vertical-slider .card-title {
  display: none;
}

.vertical-slider .slider-container {
  flex-direction: column !important;
  height: 100% !important;
  width: 100% !important;
  align-items: center !important;
  justify-content: center !important;
}

.vertical-slider .progress-slider {
  writing-mode: bt-lr;
  -webkit-appearance: slider-vertical;
  appearance: slider-vertical;
  width: 6px !important;
  height: 93% !important;
  flex: none !important;
  margin: 0 !important;
  padding: 0 !important;
}

.vertical-slider .progress-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #4a80f5;
  cursor: pointer;
}

.vertical-slider .progress-slider::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #4a80f5;
  cursor: pointer;
  border: none;
}

.vertical-slider .slider-value {
  margin-top: 10px;
  margin-bottom: 0;
  flex: none !important;
  text-align: center !important;
}

.vertical-slider .toggle-fullscreen-btn {
  margin-bottom: 10px;
  flex: none !important;
}

/* Mobile Responsive Styles */
@media (max-width: 768px) {
  :root {
    --sidebar-width: 100%;
  }

  html, body {
    overflow-x: hidden;
    width: 100%;
    position: relative;
    margin: 0;
    padding: 0;
  }

  #root {
    overflow-x: hidden;
    width: 100%;
    max-width: 100vw;
  }

  /* Main app container becomes column layout */
  .app-container {
    flex-direction: column;
    overflow-x: hidden;
    width: 100%;
    max-width: 100vw;
    box-sizing: border-box;
  }

  /* Sidebar moves to bottom */
  .sidebar {
    width: 100%;
    max-width: 100vw;
    height: auto;
    max-height: 50vh;
    order: 2;
    border-right: none;
    border-top: 1px solid var(--border-color);
    padding: 8px;
    overflow-y: auto;
    overflow-x: hidden;
    box-sizing: border-box;
  }

  /* 3D view takes top portion */
  .main-content {
    order: 1;
    height: 50vh;
    width: 100%;
    max-width: 100vw;
    overflow: hidden;
    box-sizing: border-box;
  }

  /* Cards more compact on mobile */
  .card {
    padding: 10px;
    margin: 6px 0;
    touch-action: manipulation;
    box-sizing: border-box;
    width: 100%;
    max-width: 100%;
  }

  .card-title {
    font-size: 14px;
    margin-bottom: 8px;
    padding-bottom: 4px;
    box-sizing: border-box;
  }

  /* InputForm compact layout */
  .input-form-grid {
    display: flex;
    flex-wrap: nowrap;
    gap: 6px;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    overflow-x: auto;
  }

  .input-form-grid label:not(.input-form-label) {
    display: none; /* Hide unit labels after inputs */
  }

  .input-form-label {
    width: auto;
    font-size: var(--font-size-small);
    margin-left: 0;
    margin-right: 4px;
    box-sizing: border-box;
    flex-shrink: 0;
    white-space: nowrap;
  }

  .number-input {
    flex: 1;
    min-width: 50px;
    max-width: 70px;
    padding: 4px 6px;
    font-size: var(--font-size-small);
    box-sizing: border-box;
    margin-right: 4px;
  }

  /* Options list horizontal scroll */
  .options-container {
    display: flex;
    overflow-x: auto;
    gap: 8px;
    padding: 8px 0;
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: x mandatory;
    scrollbar-width: thin;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
  }

  .options-container::-webkit-scrollbar {
    height: 4px;
  }

  .options-container::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 2px;
  }

  .options-container::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 2px;
  }

  .options-container::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  .option-card {
    flex: 1;
    min-width: calc(50% - 4px);
    flex-shrink: 0;
    padding: 8px;
    margin: 0;
    scroll-snap-align: start;
    touch-action: manipulation;
    box-sizing: border-box;
    border: 2px solid #ddd;
    cursor: pointer;
    background-color: #fff;
  }

  .option-card[data-selected="true"],
  .option-card[data-selected="True"] {
    border: 2px solid #000 !important;
  }

  .option-card[data-selected="false"],
  .option-card[data-selected="False"] {
    border: 2px solid #ddd !important;
  }

  .option-card:only-child {
    max-width: 100%;
  }

  .option-card:not(:only-child) {
    max-width: calc(50% - 4px);
  }

  .option-card > div:first-child {
    margin-bottom: 4px;
    box-sizing: border-box;
  }

  .option-card > div:last-child {
    gap: 2px 8px;
    font-size: var(--font-size-small);
    box-sizing: border-box;
  }

  /* Visualization options side by side */
  .visualization-options-mobile {
    display: flex;
    flex-direction: row;
    gap: 12px;
    justify-content: space-between;
  }

  .visualization-options-mobile .toggle-container {
    margin: 0;
    flex-direction: row;
    align-items: center;
    gap: 0;
    flex: 1;
  }

  .visualization-options-mobile .toggle-container + .toggle-container {
    margin-top: 0;
  }

  .toggle-label {
    font-size: var(--font-size-small);
    line-height: 1.2;
    white-space: nowrap;
  }

  /* Toggle switches */
  .toggle-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 8px;
  }

  .toggle-switch {
    position: relative;
    display: inline-block;
    width: 44px;
    height: 24px;
    flex-shrink: 0;
  }

  .toggle-switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.3s;
    border-radius: 24px;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 3px;
    top: 3px;
    background-color: white;
    transition: 0.3s;
    border-radius: 50%;
  }

  input:checked + .slider {
    background-color: var(--primary-color);
  }

  input:checked + .slider:before {
    transform: translateX(20px);
  }

  .toggle-label {
    font-size: var(--font-size-small);
    user-select: none;
  }

  /* Component table responsive */
  .component-table {
    font-size: var(--font-size-small);
    display: block;
    overflow-x: auto;
    white-space: nowrap;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
  }

  .component-table thead,
  .component-table tbody,
  .component-table tr {
    display: table;
    width: 100%;
    table-layout: fixed;
    box-sizing: border-box;
  }

  .component-table th,
  .component-table td {
    padding: 6px 2px;
    font-size: var(--font-size-small);
  }

  .component-image {
    width: 24px;
    height: 24px;
    margin-right: 4px;
  }

  .component-color-bar {
    width: 4px;
    height: 24px;
  }

  /* Unit selector more compact */
  .unit-dropdown {
    font-size: var(--font-size-small);
    padding: 2px 1.5rem 2px 0.5rem;
    min-width: 55px;
    height: 22px;
  }

  /* Fullscreen slider adjustments */
  .fullscreen-slider {
    left: 2%;
    right: 2%;
    bottom: 10px;
  }
  
  /* Mobile vertical slider on left side */
  .fullscreen-slider.mobile-vertical {
    /* position: fixed !important; */
    left: 10px !important;
    right: auto !important;
    top: 56px !important;
    bottom: auto !important;
    /* transform: translateY(-50%) !important; */
    width: 35px !important;
    max-width: 35px !important;
    height: calc(100% - 56px - 65px) !important; /* Subtract space for player controls at bottom */
    max-height: calc(100% - 56px - 65px) !important;
    padding: 6px 8px !important;
    flex-direction: column !important;
    justify-content: center !important;
    background: rgba(0, 0, 0, 0.2) !important;
    gap: 4px !important;
  }
  
  /* .fullscreen-slider.mobile-vertical:hover {
    background: rgba(0, 0, 0, 0.6) !important;
  } */
  
  .fullscreen-slider.mobile-vertical .orientation-toggle-button {
    display: flex !important;
    order: -1;
    margin-bottom: 4px;
    transform: rotate(90deg);
    font-size: 30px !important;
  }
  
  .fullscreen-slider.mobile-vertical .slider-container {
    flex-direction: column !important;
    gap: 2px !important;
    height: 100% !important;
    width: 100% !important;
    align-items: center !important;
    justify-content: center !important;
  }
  
  .fullscreen-slider.mobile-vertical .playback-controls {
    display: none !important; /* Hide controls inside slider container */
  }
  
  .fullscreen-slider.mobile-vertical .progress-slider {
    writing-mode: bt-lr !important;
    -webkit-appearance: slider-vertical !important;
    appearance: slider-vertical !important;
    width: 6px !important;
    height: 100% !important;
    flex: 1 !important;
    margin: 0 !important;
    background: rgba(255, 255, 255, 0.3) !important;
    border-radius: 3px !important;
  }
  
  .fullscreen-slider.mobile-vertical .progress-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 18px;
    height: 18px;
    background: rgba(255, 215, 0, 0.6);
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s ease;
  }
  
  .fullscreen-slider.mobile-vertical .progress-slider::-webkit-slider-thumb:hover {
    transform: scale(1.2);
    background: var(--highlight-primary);
  }
  
  .fullscreen-slider.mobile-vertical .progress-slider::-webkit-slider-thumb:active {
    background: var(--highlight-primary);
  }
  
  .fullscreen-slider.mobile-vertical .progress-slider::-moz-range-thumb {
    width: 18px;
    height: 18px;
    background: rgba(255, 215, 0, 0.6);
    border-radius: 50%;
    cursor: pointer;
    border: none;
    transition: all 0.2s ease;
  }
  
  .fullscreen-slider.mobile-vertical .progress-slider::-moz-range-thumb:hover {
    transform: scale(1.2);
    background: var(--highlight-primary);
  }
  
  .fullscreen-slider.mobile-vertical .progress-slider::-moz-range-thumb:active {
    background: var(--highlight-primary);
  }
  
  .fullscreen-slider.mobile-vertical .slider-value {
    display: none !important;
  }
  
  /* Grey checkpoints in mobile vertical mode */
  .fullscreen-slider.mobile-vertical ~ * .progress-checkpoint,
  .fullscreen-slider.mobile-vertical .progress-checkpoint {
    background-color: rgba(0, 0, 0, 0.2) !important;
    left: -1.8px;
    width: 9px;
    height: 9px;

  }
  
  .fullscreen-slider.mobile-vertical ~ * .progress-checkpoint:hover,
  .fullscreen-slider.mobile-vertical .progress-checkpoint:hover {
    background-color: rgba(0, 0, 0, 0.6) !important;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.4) !important;
  }
  
  /* Separate horizontal controls below vertical slider */
  .mobile-vertical-controls {
    display: none; /* Hidden by default */
  }
  
  @media (max-width: 768px) {
    .fullscreen-slider.mobile-vertical ~ .mobile-vertical-controls {
      display: flex !important;
      position: absolute !important;
      left: 10px !important;
      bottom: 10px !important;
      flex-direction: row !important;
      gap: 6px !important;
      padding: 6px 8px !important;
      background: rgba(0, 0, 0, 0.2) !important;
      border-radius: 8px !important;
      z-index: 9999 !important;
      align-items: center !important;
    }
    
    .fullscreen-slider.mobile-vertical ~ .mobile-vertical-controls:hover {
      background: rgba(0, 0, 0, 0.6) !important;
    }
    
    .fullscreen-slider.mobile-vertical ~ .mobile-vertical-controls .playback-button {
      font-size: 30px !important;
      background: transparent;
      border: none;
      cursor: pointer;
      padding: 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: rgba(255, 255, 255, 0.8);
      transition: all 0.2s ease;
      min-width: 0px;
    }
    
    .fullscreen-slider.mobile-vertical ~ .mobile-vertical-controls .playback-button:hover {
      color: rgba(255, 255, 255, 1);
      transform: scale(1.1);
    }
    
    .fullscreen-slider.mobile-vertical ~ .mobile-vertical-controls .playback-button:active {
      transform: scale(0.95);
    }
    
    .fullscreen-slider.mobile-vertical ~ .mobile-vertical-controls .playback-button.speed-button {
      font-size: 18px !important;
      font-family: 'Lato', 'Roboto', 'Arial', sans-serif;
    }
  }
  
  /* Mobile horizontal slider at bottom */
  .fullscreen-slider.mobile-horizontal {
    left: 2% !important;
    right: 2% !important;
    bottom: 10px !important;
    top: auto !important;
    transform: none !important;
    width: auto !important;
    max-width: none !important;
    height: 35px !important;
    max-height: 35px !important;
    flex-direction: row !important;
    padding: 4px 8px !important;
    gap: 6px !important;
  }
  
  .fullscreen-slider.mobile-horizontal .orientation-toggle-button {
    display: flex !important;
    order: 100;
    margin-left: 0px;
    font-size: 30px !important;
  }
  
  .fullscreen-slider.mobile-horizontal .slider-container {
    flex-direction: row !important;
    width: 100% !important;
    height: auto !important;
    gap: 6px !important;
  }
  
  .fullscreen-slider.mobile-horizontal .playback-controls {
    flex-direction: row !important;
    gap: 4px !important;
    margin-right: 6px !important;
  }
  
  .fullscreen-slider.mobile-horizontal .playback-button {
    font-size: 30px !important;
  }
  
  .fullscreen-slider.mobile-horizontal .playback-button.speed-button {
    font-size: 18px !important;
  }
  
  .fullscreen-slider.mobile-horizontal .progress-slider {
    writing-mode: horizontal-tb !important;
    -webkit-appearance: none !important;
    appearance: none !important;
    width: 100% !important;
    height: 6px !important;
    flex: 1 !important;
    background: rgba(255, 255, 255, 0.3) !important;
    border-radius: 3px !important;
  }
  
  .fullscreen-slider.mobile-horizontal .progress-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 18px;
    height: 18px;
    background: rgba(255, 215, 0, 0.6);
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s ease;
  }
  
  .fullscreen-slider.mobile-horizontal .progress-slider::-webkit-slider-thumb:hover {
    transform: scale(1.2);
    background: var(--highlight-primary);
  }
  
  .fullscreen-slider.mobile-horizontal .progress-slider::-webkit-slider-thumb:active {
    background: var(--highlight-primary);
  }
  
  .fullscreen-slider.mobile-horizontal .progress-slider::-moz-range-thumb {
    width: 18px;
    height: 18px;
    background: rgba(255, 215, 0, 0.6);
    border-radius: 50%;
    cursor: pointer;
    border: none;
    transition: all 0.2s ease;
  }
  
  .fullscreen-slider.mobile-horizontal .progress-slider::-moz-range-thumb:hover {
    transform: scale(1.2);
    background: var(--highlight-primary);
  }
  
  .fullscreen-slider.mobile-horizontal .progress-slider::-moz-range-thumb:active {
    background: var(--highlight-primary);
  }

  /* Collapsible cards */
  .card-content {
    overflow: hidden;
    transition: max-height 0.3s ease, opacity 0.3s ease;
  }

  .card-content.collapsed {
    max-height: 0;
    opacity: 0;
    padding: 0;
    margin: 0;
  }

  .card-content.expanded {
    max-height: 1000px;
    opacity: 1;
  }

  /* Collapsible title styling */
  .collapsible-title {
    cursor: pointer;
    user-select: none;
    padding: 4px 0;
  }

  .collapsible-title:active {
    opacity: 0.7;
  }
}

/* Desktop: Keep visualization options in column */
@media (min-width: 769px) {
  .visualization-options-mobile {
    display: flex;
    flex-direction: column;
    gap: 0;
  }

  .visualization-options-mobile .toggle-container {
    flex-direction: row;
    align-items: center;
    margin-bottom: 12px;
  }

  .visualization-options-mobile .toggle-container:last-child {
    margin-bottom: 0;
  }

  .toggle-label {
    font-size: var(--font-size-normal);
  }
}

/* Board Type Filter Styles */
.filter-controls {
  display: flex;
  gap: 8px;
  margin-bottom: 12px;
}

.filter-button {
  background-color: var(--light-bg-color);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 4px 8px;
  font-size: var(--font-size-small);
  color: var(--text-color);
  cursor: pointer;
  transition: var(--transition);
}

.filter-button:hover {
  background-color: var(--hover-bg);
  border-color: var(--primary-light);
}

.board-type-list {
  margin-bottom: 12px;
}

.board-type-item {
  margin-bottom: 8px;
}

.board-type-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: var(--font-size-normal);
  transition: var(--transition);
}

.board-type-label:hover {
  opacity: 0.8;
}

.board-type-checkbox {
  margin-right: 8px;
  cursor: pointer;
}

.board-color-indicator {
  width: 12px;
  height: 12px;
  margin-right: 8px;
  border: 1px solid #ccc;
  border-radius: 2px;
  flex-shrink: 0;
}

.board-type-info {
  display: flex;
  align-items: center;
  gap: 8px;
}

.board-type-name {
  font-weight: 600;
  color: var(--text-color);
}

.board-type-price {
  color: var(--secondary-text);
  font-size: var(--font-size-normal);
}

.filter-summary {
  margin-top: 12px;
  font-size: var(--font-size-small);
  color: var(--secondary-text);
  font-style: italic;
}

/* Collapsible functionality */
.collapsible-title {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  user-select: none;
  padding: 2px 4px;
  margin: -2px -4px;
  border-radius: var(--border-radius);
  transition: background-color 0.2s ease;
}

.collapsible-title:hover {
  background-color: var(--hover-bg);
}

.collapse-icon {
  font-size: 16px;
  color: var(--secondary-text);
  margin-left: 8px;
  display: inline-flex;
  align-items: center;
  flex-shrink: 0;
  line-height: 1;
}

/* Required board types styling */
.board-type-label.required {
  opacity: 0.6;
  cursor: not-allowed;
}

.board-type-label.required .board-type-checkbox {
  cursor: not-allowed;
}

.required-indicator {
  font-size: var(--font-size-small);
  color: var(--secondary-text);
  font-weight: normal;
  font-style: italic;
}
